"use client"

import { useState } from "react"
import Link from "next/link"
import { usePathname } from "next/navigation"
import { Button } from "@/components/ui/button"
import {
  Sheet,
  SheetContent,
  SheetTrigger,
} from "@/components/ui/sheet"
import {
  NavigationMenu,
  NavigationMenuContent,
  NavigationMenuItem,
  NavigationMenuList,
  NavigationMenuTrigger,
} from "@/components/ui/navigation-menu"

const categories = [
  {
    name: "Vestidos",
    subcategories: ["Casuales", "Formales", "De Fiesta"]
  },
  {
    name: "Blusas",
    subcategories: ["Manga Corta", "Manga Larga", "Sin Manga"]
  },
  {
    name: "Zapatillas",
    subcategories: ["Deportivas", "Casuales", "Elegantes"]
  },
  {
    name: "Accesorios",
    subcategories: ["Carteras", "Billeteras", "Mochilas"]
  }
]

export default function Navigation() {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false)
  const pathname = usePathname()

  const isActive = (path: string) => pathname === path

  return (
    <header className="sticky top-0 z-50 w-full border-b bg-white">
      <div className="container mx-auto px-4">
        <div className="flex h-16 items-center justify-between">
          {/* Logo */}
          <Link href="/" className="flex items-center space-x-2">
            <span className="text-2xl font-bold text-pink-600">Hose Pink</span>
            <span className="text-xl">Boutique</span>
          </Link>

          {/* Desktop Navigation */}
          <div className="hidden md:flex items-center space-x-6">
            <NavigationMenu>
              <NavigationMenuList>
                <NavigationMenuItem>
                  <Link 
                    href="/" 
                    className={`px-3 py-2 text-sm font-medium ${
                      isActive("/") ? "text-pink-600" : "text-gray-700 hover:text-pink-600"
                    }`}
                  >
                    Inicio
                  </Link>
                </NavigationMenuItem>

                <NavigationMenuItem>
                  <NavigationMenuTrigger>Categorías</NavigationMenuTrigger>
                  <NavigationMenuContent>
                    <div className="grid gap-3 p-4 w-[400px] md:w-[500px] lg:w-[600px]">
                      {categories.map((category) => (
                        <div key={category.name} className="space-y-2">
                          <h3 className="font-medium text-gray-900">{category.name}</h3>
                          <div className="grid grid-cols-2 gap-2">
                            {category.subcategories.map((subcategory) => (
                              <Link
                                key={subcategory}
                                href={`/category/${category.name.toLowerCase()}/${subcategory.toLowerCase()}`}
                                className="text-sm text-gray-600 hover:text-pink-600"
                              >
                                {subcategory}
                              </Link>
                            ))}
                          </div>
                        </div>
                      ))}
                    </div>
                  </NavigationMenuContent>
                </NavigationMenuItem>

                <NavigationMenuItem>
                  <Link 
                    href="/new-arrivals"
                    className={`px-3 py-2 text-sm font-medium ${
                      isActive("/new-arrivals") ? "text-pink-600" : "text-gray-700 hover:text-pink-600"
                    }`}
                  >
                    Nuevos Ingresos
                  </Link>
                </NavigationMenuItem>

                <NavigationMenuItem>
                  <Link 
                    href="/offers"
                    className={`px-3 py-2 text-sm font-medium ${
                      isActive("/offers") ? "text-pink-600" : "text-gray-700 hover:text-pink-600"
                    }`}
                  >
                    Ofertas
                  </Link>
                </NavigationMenuItem>
              </NavigationMenuList>
            </NavigationMenu>

            <Button variant="outline" className="hidden lg:flex">
              Buscar
            </Button>
          </div>

          {/* Mobile Menu Button */}
          <Sheet open={isMobileMenuOpen} onOpenChange={setIsMobileMenuOpen}>
            <SheetTrigger asChild>
              <Button variant="ghost" className="md:hidden">
                <span className="sr-only">Abrir menú</span>
                ☰
              </Button>
            </SheetTrigger>
            <SheetContent side="right" className="w-[300px] sm:w-[400px]">
              <nav className="flex flex-col space-y-4">
                <Link
                  href="/"
                  className="text-lg font-medium hover:text-pink-600"
                  onClick={() => setIsMobileMenuOpen(false)}
                >
                  Inicio
                </Link>
                
                <div className="space-y-2">
                  <h3 className="text-lg font-medium">Categorías</h3>
                  {categories.map((category) => (
                    <div key={category.name} className="ml-4 space-y-2">
                      <h4 className="font-medium">{category.name}</h4>
                      <div className="ml-4 space-y-1">
                        {category.subcategories.map((subcategory) => (
                          <Link
                            key={subcategory}
                            href={`/category/${category.name.toLowerCase()}/${subcategory.toLowerCase()}`}
                            className="block text-sm text-gray-600 hover:text-pink-600"
                            onClick={() => setIsMobileMenuOpen(false)}
                          >
                            {subcategory}
                          </Link>
                        ))}
                      </div>
                    </div>
                  ))}
                </div>

                <Link
                  href="/new-arrivals"
                  className="text-lg font-medium hover:text-pink-600"
                  onClick={() => setIsMobileMenuOpen(false)}
                >
                  Nuevos Ingresos
                </Link>

                <Link
                  href="/offers"
                  className="text-lg font-medium hover:text-pink-600"
                  onClick={() => setIsMobileMenuOpen(false)}
                >
                  Ofertas
                </Link>

                <Button className="w-full" variant="outline">
                  Buscar
                </Button>
              </nav>
            </SheetContent>
          </Sheet>
        </div>
      </div>
    </header>
  )
}
